var _self,
	page = 1,
	timer = null;
export default {
	data() {
		return {
			baseUrl: '',
			lists: [],
			loadingText: '加载中...',
			userToken: false
		};
	},
	filters: {
		formatDate: function(value) {
			var date = new Date(value); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
			var Y = date.getFullYear() + '-';
			var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
			var D = date.getDate() + ' ';
			var h = date.getHours() + ':';
			var m = date.getMinutes() + ':';
			var s = date.getSeconds();
			return Y + M + D + h + m + s;
		}
	},
	computed: {
		i18n() {
			return this.$t('index');
		}
	},
	onLoad: function() {
		_self = this;
		console.log('1');
		this.getnewsList();
		let token = uni.getStorageSync('storage_token');
		if (token == '' || token == null || token == undefined) {
			this.userToken = false;
		} else {
			this.userToken = true;
		}
		
		uni.setNavigationBarTitle({
			title: this.$t('index.jilus')
		})	
	},
	// 下拉
	onPullDownRefresh: function() {
		console.log('2');
		this.getnewsList();
	},
	// 上拉
	onReachBottom: function() {
		console.log('3');
		if (timer != null) {
			console.log('4');
			clearTimeout(timer);
		}
		timer = setTimeout(function() {
			_self.getmorenews();
		}, 1000);
	},
	methods: {
		go_login() {
			uni.navigateTo({
				url: '../login/login'
			});
		},
		country(id,type) {
			uni.navigateTo({
				url: '../jilujieguo/jilujieguo?id=' + id + "&leixing="+type
			});
		
		},
		// 上拉下拉时调用
		getmorenews: function() {
			this.baseUrl = this.websiteUrl;
			let token = uni.getStorageSync('storage_token');
			// console.log(this.baseUrl);
			// console.log(token);
			if (_self.loadingText != '' && _self.loadingText != '加载更多') {
				return false;
			}
			_self.loadingText = '加载中...';
			uni.showNavigationBarLoading();
			uni.request({
				url: this.baseUrl + '/api/log?is_collect=0&count=10&page=' + page,
				method: 'GET',
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					token: token
				},
				success: function(res) {
					_self.loadingText = '';
					if (res.data.data == null) {
						uni.hideNavigationBarLoading();
						_self.loadingText = '已加载全部';
						return false;
					}
					page++;
					console.log(res);
					_self.lists = _self.lists.concat(res.data.data);

					_self.loadingText = '加载更多';
					uni.hideNavigationBarLoading();
				}
			});
		},
		// 初始化时调用/
		getnewsList: function() {
			this.baseUrl = this.websiteUrl;
			let token = uni.getStorageSync('storage_token');
			// console.log(this.baseUrl);
			// console.log(token);
			page = 1;
			uni.showNavigationBarLoading();
			uni.request({
				url: this.baseUrl + '/api/log?is_collect=0&count=10&page=' + page,
				method: 'GET',
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					token: token
				},
				success: function(res) {
					page++;
					// _self.newsList = res.data.split('--hcSplitor--');
					// console.log(res.data.data[0].airport1);
					_self.lists = res.data.data;
					uni.hideNavigationBarLoading();
					uni.stopPullDownRefresh();
					_self.loadingText = '加载更多';
				}
			});
		},
		shouca() {
			uni.showToast({
				title: '您已经收藏过了',
				icon: 'none',
				duration: 800
			});
		},
		shouchang(e) {
			console.log(e);
			var that = this;
			this.baseUrl = this.websiteUrl;
			let token = uni.getStorageSync('storage_token');

			uni.request({
				url: this.baseUrl + '/api/collect',
				data: {
					ids: e,
					status: '1'
				},
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					token: token
				},
				success: function(res) {
					if (res.data.code == 200) {
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 800
						});
						that.getnewsList();
					} else {
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 800
						});
					}
				}
			});
		},
		onNavigationBarButtonTap() {
			console.log('你点击了按钮');
		}
	}
};
